.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
}

input {
  background: #ccc;
  color: #000;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 10px;
  font-weight: bold;
}

.field {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 10px;
}

.researchSettings {
  display: -ms-flexbox;
  display: flex;
}

.researchSettings div {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 10px;
}

.workspace {
  display: -ms-flexbox;
  display: flex;
}

.workItems {
  -ms-flex: 2 1;
      flex: 2 1;
}

.workingspace {
  -ms-flex: 3 1;
      flex: 3 1;
}

.center {
  text-align: center;
}



